/*  Dropdown menu*/

.drop-menu:hover > .dropdown-menu {
    display: block !important;
    -webkit-animation: fadeInUp 400ms;
    animation: fadeInUp 400ms;
}

.dropdown-menu {
    position: absolute;
    top: 30px;
    left: 0;
    background: rgba(0, 0, 0, 0.6);
    list-style: none;
    padding: 0;
    margin: 0;
    width: 220px;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.1);
    display: none;
    z-index: 999;
}

.dropdown .dropdown-menu a .active {
    color: #fdb45e;
    padding-left: 0;
}

.drop-menu .dropdown-menu a {
    padding: 10px 20px 0;
}

.drop-menu .dropdown-menu a:last-child {
    padding-bottom: 20px;
}

.drop-menu .dropdown-menu a {
    color: #fff;
}

.drop-menu .dropdown-menu a:hover {
