    -webkit-animation: showDivList 1s ease both;
            animation: showDivList 1s ease both;
}

.list-cat.active > a {
    font-size: 17px;
    font-weight: 500;
    padding: 10px 20px 0;
    -webkit-animation: showList 1s ease both;
            animation: showList 1s ease both;
}

.dflex h2::before {
    content: none;
}
.dflex h2::after {
    content: none;
}
@keyframes showDivList {
    from {
        height: 0;
    }
    to {
        height: auto;
    }
}
/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes showDivList {
    from {
        height: 0;
    }
    to {
        height: auto;
    }
}

/* Internet Explorer */

/* Opera < 12.1 */
@keyframes showList {
    from {
        font-weight: 0;
        padding: 0 20px;
        height: 0;
        color: #fff;
        opacity: 0;
    }
    to {
        opacity: 1;

        height: auto;
        font-size: 17px;
        font-weight: 500;
        padding: 10px 20px 0;
    }
}
/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes showList {
    from {
        font-weight: 0;
        padding: 0 20px;
        height: 0;
        color: #fff;
        opacity: 0;
    }
    to {
        opacity: 1;
        height: auto;
        font-size: 17px;
        font-weight: 500;
        padding: 10px 20px 0;
    }
}

