.video-gallery {
    margin-top: 57px;
    position: inherit;
}

.video-gallery a img {
    height: 100%;
    width: 100%;
}

.iframe-img {
    position: relative;
    display: block;
    height: 61px;
    margin-bottom: 10px;
    border: 2px solid #ccccc6;
    border-radius: 3px;
}

.overlay-icon {
    position: absolute;
    top: 0;
    width: 100%;
    height: 61px;
    background: #fe980f;
    border-radius: 3px;
    color: #fff;
    font-size: 20px;
    line-height: 0;
    display: block;
    opacity: 0;
    transition: 300ms;
}

.overlay-icon i {
    position: relative;
    top: 50%;
    margin-top: -20px;
}

.video-gallery a:hover .overlay-icon {
    opacity: 1;
}

.video-gallery p {
    color: #8c8c88;
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 0px;
}

